@tailwind base;
@tailwind components;
@tailwind utilities;

/* Add this to your global CSS file or Tailwind CSS configuration */
.section-striped {
    background: repeating-linear-gradient(
      45deg,
      #f5f5f5,
      #f5f5f5 10px,
      #eaeaea 10px,
      #eaeaea 20px
    );
  }


  .gradient-bg {
    background: linear-gradient(0.25turn, #eee, #ffffee, #eeffff);
    box-shadow: 0 8px 10px rgba(0, 0, 0, 0.1);
  }

  .gradient-bg-select {
    background: linear-gradient(0.25turn, #1b99c7, #c6ddb5, #c16b09);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }

  .gradient-bg-body {
    background: linear-gradient(135deg, orange 60%, cyan);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }

  
